const mapStateToProps = state => {
    const { websiteList, shopOnList} = state;
    return {
        websiteList: websiteList?.websiteList,
        authorized: state.authorized,
        shopOnList: shopOnList?.shopOnList
    }
}

const mapDispatchToProps = dispatch => {
    return {
        GET_WEBSITE_LIST: () => dispatch(GET_WEBSITE_LIST()),
        GET_ALL_SHOPON_LIST: () => dispatch(GET_ALL_SHOPON_LIST()), 
        GET_ALL_EMAIL_LIST: () => dispatch(GET_ALL_EMAIL_LIST()), 
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(editorMenu)); 
