import Swal from "sweetalert2";
import store from "../store";
import { GET_APP_DATA, SAVE_AND_PUBLISH, SET_ALL_UPDATED } from "../reducer/themes/reducersKeys"; 
import { GET_ALL_WEBSITE_LIST } from "../actions/websiteAction";

export const isWebsiteChanged = (callback) => {
    const appData = store.getState().appData
    if (appData?.is_themes_change || appData?.is_backgroun_change || appData?.is_banner_IMG_change || appData?.is_banner_change || appData?.is_card_style_change || appData?.is_theme_setting_change || appData?.is_custome_css_change || appData?.is_magic_upload_change || appData?.is_cta_setting_change || appData?.is_utm_tracker_change) {
        Swal.fire({
            title: "Changes not saved!",

            icon: "warning",
            showCancelButton: true,
            confirmButtonText: "Save & Continue",
            cancelButtonText: "Discard",
        }).then(res => {
            if (res.dismiss != "backdrop") {
                if (res.isConfirmed) {
                    store.dispatch({ type: SAVE_AND_PUBLISH, payload: true })
                    store.dispatch({ type: SET_ALL_UPDATED, payload: false })
                    setTimeout(() => {
                    //    store.dispatch(GET_ALL_WEBSITE_LIST())                                                                                  
                        callback()
                    }, 500);
                }
                else {
                    DiscardSettings(callback)
                    callback()
                }
            }
        })
    }
    else {
        callback()
    }
};

const DiscardSettings = (callback) => {
    const btn = document.querySelector('#discardWebsiteSetting')
    if(btn){
        btn.click()
        callback()
    }
}