import React, { Component } from 'react'
import HomeMenu from './homeMenu'
import EditorMenu from './editorMenu'
import Logo from './logo'

export default class menus extends Component {

    componentDidMount() {

            document.addEventListener('click', function (event) {
              var target = event.target;
              if (!target.closest('.menu-item-submenu')) {
                var menuItems = document.querySelectorAll('.menu-item-submenu');
                for (var i = 0; i < menuItems.length; i++) {
                  menuItems[i].classList.remove('menu-item-open');
                }
              }
            });
    
            var menuItems = document.querySelectorAll('.menu-item-submenu');
            for (var i = 0; i < menuItems.length; i++) {
              menuItems[i].addEventListener('click', function (event) {
                var target = event.target;
                var menuItems = document.querySelectorAll('.menu-item-submenu');
                for (var i = 0; i < menuItems.length; i++) {
                  if(target != menuItems[i])menuItems[i].classList.remove('menu-item-open');
                }
                target.classList.toggle('menu-item-open');
              });
            }
    
    }

    render() {
        return (
            <div className="aside aside-left  aside-fixed  d-flex flex-column flex-row-auto" id="kt_aside">
                <Logo />
                <div className="aside-menu-wrapper flex-column-fluid" id="kt_aside_menu_wrapper">
                    {/* <HomeMenu /> */}
                    <EditorMenu />
                </div>
            </div>
        )
    }
}
