import axios from 'axios';
import HttpClient from "./http-client";
import { CREATE_WEBSITE_API, UPDATE_WEBSITE_API, ALL_WEBSITE_LIST_API, DELETE_WEBSITE_API } from './api'
import { CREATE_WEBSITE_REDUCER, UPDATE_WEBSITE_REDUCER, ALL_WEBSITE_LIST_REDUCER, DELETE_WEBSITE_REDUCER, ACTIVE_WEBSITE_ID_REDUCER, LOADING_WEBSITE_REDUCER, UPDATE_WEBSITE_UTM_TRACKER, DISPATCH_GET_WALL_DETAILS } from '../reducer/keys';
import { TOAST_DISMISS, TOAST_ERROR, TOAST_SUCCESS } from '../utils';
import { GET_APP_DATA } from '../reducer/themes/reducersKeys';
import { CHANGE_WEBSITE_WALL } from './themeActions/themeActions';
import store from '../store';
import { UPDATE_EXTRA_DATA } from './initActions';




export const CREATE_WEBSITE = (name, callback, navigate) => {
    var obj = { "name": name , "wallId":localStorage.getItem('active_wall')}
    return (dispatch) => {
        new HttpClient().post(CREATE_WEBSITE_API, obj).then((response) => {
            const { responseData, responseCode, responseMessage } = response.data
            if (responseCode == 200) {
                localStorage.setItem('website_id', responseData?.wall?.Wall?.id)
                UPDATE_EXTRA_DATA({ website_id: responseData?.wall?.Wall?.id })
                TOAST_DISMISS()
                TOAST_SUCCESS(responseMessage)
                dispatch({ type: CREATE_WEBSITE_REDUCER, payload: responseData });
                dispatch({ type: ACTIVE_WEBSITE_ID_REDUCER, payload: responseData?.wall?.Wall?.id });
                window.location.pathname.includes("/shopify/") ? navigate(`/website/shopify/${localStorage.getItem('website_id')}`) :
                    window.location.pathname.includes("/bigcommerce/") ? navigate(`/website/bigcommerce/${localStorage.getItem('website_id')}`) :
                        navigate(`/website/publish/${localStorage.getItem('website_id')}`)
                callback(responseCode, responseMessage);
            }
        }).catch(error => {
            const { data } = error.response;
            TOAST_DISMISS();
            if (data.responseCode == 404) {
                TOAST_ERROR(data.responseMessage);
                callback(data.responseCode, data.responseMessage);
            }
            else {
                TOAST_ERROR("Something went wrong!")
            }
        });
    }
}



export const UPDATE_WEBSITE = (id, name, callback) => {
    const obj = { "id": id, "name": name }

    return (dispatch) => {
        new HttpClient().post(UPDATE_WEBSITE_API, obj).then((response) => {
            const { responseData, responseCode, responseMessage } = response.data
            if (responseCode == 200) {
                TOAST_DISMISS()
                TOAST_SUCCESS(responseMessage)
                dispatch({ type: UPDATE_WEBSITE_REDUCER, payload: { id, name } });
            }
        }).catch(error => {
            const { data } = error.response;
            TOAST_DISMISS();
            if (data.responseCode == 404) {
                TOAST_ERROR(data.responseMessage);
                callback(data.responseMessage);
            }
            else {
                TOAST_ERROR("Something went wrong!")
            }
        });
    }
}


export const DELETE_WEBSITE = (id, callback, navigate) => {
    const { websiteList, dashboardData } = store.getState()
    return (dispatch) => {
        new HttpClient().post(`${DELETE_WEBSITE_API}/${id}`).then((response) => {
            const { responseData, responseCode, responseMessage } = response.data
            if (responseCode == 200) {
                TOAST_DISMISS();
                TOAST_SUCCESS(responseMessage);
                dispatch({ type: DELETE_WEBSITE_REDUCER, payload: id });
                setTimeout(() => {
                    if (websiteList.websiteList && websiteList.websiteList?.length > 1) {
                        if (id == localStorage.getItem('website_id')) {
                            if (id == websiteList.websiteList[0]?.wall?.Wall?.id) {
                                localStorage.setItem('website_id', websiteList.websiteList[1]?.wall?.Wall?.id)
                                dispatch({ type: ACTIVE_WEBSITE_ID_REDUCER, payload: websiteList.websiteList[1]?.wall?.Wall?.id });
                                UPDATE_EXTRA_DATA({ website_id: websiteList.websiteList[1]?.wall?.Wall?.id })
                                navigate(`/website/publish/${websiteList.websiteList[1]?.wall?.Wall?.id}`)
                            } else {
                                localStorage.setItem('website_id', websiteList.websiteList[0]?.wall?.Wall?.id)
                                dispatch({ type: ACTIVE_WEBSITE_ID_REDUCER, payload: websiteList.websiteList[0]?.wall?.Wall?.id });
                                UPDATE_EXTRA_DATA({ website_id: websiteList.websiteList[0]?.wall?.Wall?.id })
                                navigate(`/website/publish/${websiteList.websiteList[0]?.wall?.Wall?.id}`)
                            }
                        }
                    } else {
                        localStorage.setItem('website_id', '')
                        dispatch({ type: ACTIVE_WEBSITE_ID_REDUCER, payload: '' });
                        UPDATE_EXTRA_DATA({ website_id: '' })
                        navigate(`/website/publish/`)
                    }

                }, 1000);
            }
        }).catch(error => {
            const { data } = error.response;
            TOAST_DISMISS();
            if (data.responseCode == 404) {
                TOAST_ERROR(data.responseMessage);
                callback(data.responseMessage);
            }
            else {
                TOAST_ERROR("Something went wrong!")
            }
        });
    }
}







export const UPDATE_WEBSITE_WALL = (data) => {
    return new HttpClient().post(UPDATE_WEBSITE_API, data)
}

export const GET_WEBSITE_LIST = () => { 
    return (dispatch) => {
        dispatch({ type: LOADING_WEBSITE_REDUCER, payload: true });
        new HttpClient().get(ALL_WEBSITE_LIST_API).then((response) => {
            const { responseData, responseCode } = response.data
            if (responseCode == 200) { 
                var webData = responseData?.filter((x) => x.wall.Wall.id == localStorage.getItem('website_id'));
                var wall_id = webData[0]?.wall?.Wall?.wallId ? webData[0]?.wall?.Wall?.wallId : localStorage.getItem('wallId')
                dispatch({ type: ALL_WEBSITE_LIST_REDUCER, payload: responseData });
                dispatch({ type: ACTIVE_WEBSITE_ID_REDUCER, payload: localStorage.getItem('website_id') });
                dispatch({ type: GET_APP_DATA, payload: webData[0], heightEvent: null })
                dispatch({ type: LOADING_WEBSITE_REDUCER, payload: false }); 
                dispatch({ type: DISPATCH_GET_WALL_DETAILS, payload: webData && webData[0] ? webData[0] :  responseData[0]});
                if(wall_id) localStorage.setItem("active_wall", wall_id)
            }
        }).catch((error)=>{
            dispatch({ type: LOADING_WEBSITE_REDUCER, payload: false });
        })
    }
}

export const GET_ALL_WEBSITE_LIST = () => {
    var allwalls = store.getState()?.dashboardData?.dashboarddata?.allWalls
    return (dispatch) => {
        dispatch({ type: LOADING_WEBSITE_REDUCER, payload: true });
        new HttpClient().get(ALL_WEBSITE_LIST_API).then((response) => {
            const { responseData, responseCode } = response.data
            if (responseCode == 200) {
                var webData = responseData?.filter((x) => x.wall.Wall.id == localStorage.getItem('website_id'));
                var wall_id = webData[0]?.wall?.Wall?.wallId ? webData[0]?.wall?.Wall?.wallId : localStorage.getItem('wallId')
                dispatch({ type: ALL_WEBSITE_LIST_REDUCER, payload: responseData });
                dispatch({ type: ACTIVE_WEBSITE_ID_REDUCER, payload: localStorage.getItem('website_id') });
                dispatch({ type: GET_APP_DATA, payload: webData[0], heightEvent: null })
                dispatch({ type: LOADING_WEBSITE_REDUCER, payload: false });
                setTimeout(() => {  
                    dispatch(CHANGE_WEBSITE_WALL(webData[0]?.wall?.Wall?.wallId ? webData[0]?.wall?.Wall?.wallId : localStorage.getItem('wallId') ? localStorage.getItem('wallId') : allwalls && allwalls[0] && allwalls[0].id ? allwalls[0].id : '', webData[0]?.wall?.ThemeRule?.numberOfPosts ? webData[0]?.wall?.ThemeRule?.numberOfPosts : 20, webData[0]?.wall?.ThemeRule?.themeId))
                    localStorage.setItem("active_wall", webData[0]?.wall?.Wall?.wallId ? webData[0]?.wall?.Wall?.wallId : localStorage.getItem('wallId') ? localStorage.getItem('wallId') : allwalls && allwalls[0] && allwalls[0].id ? allwalls[0].id : '')
                }, 500);

            }

        }).catch((error)=>{
            dispatch({ type: LOADING_WEBSITE_REDUCER, payload: false });
        })
    }
}



export const UPDATE_UTM_TRACKER = ({googleTackingCode, googleTackingStatus, UtmTackingStatus, utmSource, utmCampaign, utmMedium}) => {
    let obj = {'id': localStorage.getItem('website_id'), 'google_tacking_code':googleTackingCode, 'google_tacking_status':Number(googleTackingStatus), 'utm_status':Number(UtmTackingStatus), 'utm_source':utmSource, 'utm_campaign':utmCampaign, 'utm_medium':utmMedium}
  
    return (dispatch) => {
        new HttpClient().post(UPDATE_WEBSITE_API, obj).then((response) => {
            const { responseData, responseCode, responseMessage } = response.data
            if (responseCode == 200) {
                dispatch({ type: UPDATE_WEBSITE_UTM_TRACKER, payload: obj });
            }
        })
    }
}