import React, { useState, useEffect, useCallback, useRef } from 'react';
import { shallowEqual, useDispatch, useSelector } from 'react-redux';
import { Link, useNavigate } from 'react-router-dom';
import { Dropdown } from 'react-bootstrap';
import { GALLERY_WALL_UPDATE, TAGGBOX_USER_HOME_DATA, TAGGSHOP_SHOPPABLE_GALLERY_DATA } from '../../../actions/sceneActions';
import {  getModeration, removeAllFilterBody } from '../../../actions/moderationActions';
import { closePricingModal } from '../../../utils/close_modal';
import { UPDATE_EXTRA_DATA } from '../../../actions/initActions';
import { GET_FEEDS } from '../../../actions/manageFeedsAction';
import { CHANGE_MODERATION_WALLID, DELETE_WALL, SHOPPABLE_GALLERY_DATA } from '../../../reducer/keys';
import Swal from 'sweetalert2'
import { TOAST_SUCCESS } from '../../../utils';
import { Button, Tooltip, OverlayTrigger } from "react-bootstrap";

function WallSwitcher(props) {
    const {galleryName, onGalleryName, onAddGalleryWall, changeId, item} = props
    const outsideNewRef = useRef(null);
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const wallId = localStorage.getItem("wallId")
    const {  allWalls } = useSelector(state => state.dashboardData.dashboarddata, shallowEqual);
    const { user } = useSelector(state => state.authorized?.userData.user_data, shallowEqual);
    const [selectedWall, setSelectedWall] = useState(null);
    const [editPopover, setEditPopover] = useState();
    const [editGallery_Popover, setEditGallery_Popover] = useState();

    const [statusWall, setStatusWall] = useState(item.status); 
    const [autoUpdateWall, setAutoUpdateWall] = useState(item.autoUpdate);

    
    const lastWallId = localStorage.getItem("wallId")
    // const lastWallId = user.extra_data ? JSON.parse(user.extra_data)?.wallId : null

    const addAnotherWidget = useCallback(() => {
        // navigate('/walls');
        // closePricingModal(dispatch);
        // dispatch(openCreateNewWallPopup(true));
    }, []);

 
    const selectOtherWidget = useCallback((widget, feedCount) => {
        setSelectedWall(widget);
        if(user.role == 'Collaborator'){
            UPDATE_EXTRA_DATA({ wallId: widget.wallId })
            localStorage.setItem("wallId", widget.wallId)    
        }
        else{
            UPDATE_EXTRA_DATA({ wallId: widget.id })
            localStorage.setItem("wallId", widget.id)
        }
        dispatch({ type: CHANGE_MODERATION_WALLID, payload: true }); 
        dispatch(getModeration())
        dispatch(removeAllFilterBody(true))
        dispatch(GET_FEEDS())
        navigate(`/content/moderation/${widget.id}`);
        closePricingModal(dispatch)
        // }
    }, []);


    useEffect(() => {
        var wall = null
        if (lastWallId) {
            wall = allWalls && allWalls.length ? allWalls.find(w => w.id == lastWallId) : null
        }
        else {
            wall = allWalls && allWalls.length ? allWalls[0] : null;
            if(user.role == 'Collaborator'){
                UPDATE_EXTRA_DATA({ wallId: wall.wallId })
                localStorage.setItem("wallId", wall.wallId)    
            }
            else{
                UPDATE_EXTRA_DATA({ wallId: wall.id })
                localStorage.setItem("wallId", wall.id)
            }
        }
        setSelectedWall(wall)

    }, [lastWallId,allWalls])




    useEffect(() => {
        function handleClickOutside(event) {
          if (outsideNewRef.current && !outsideNewRef.current.contains(event.target)) { setEditPopover('');  }
        }
    
        document.addEventListener('mousedown', handleClickOutside);
        return () => {
          document.removeEventListener('mousedown', handleClickOutside);
        };
      }, []);

const deleteGallery=(id)=>{
    const item = allWalls && allWalls.length ? allWalls.find(w => w.id == id) : null
    Swal.fire({
        title: "Are You Sure?",
        text:  'Are you sure you want to permanenetly delete this Website template?',
        icon: "warning",
        showCancelButton: true,
        cancelButtonText: "Cancel",
        confirmButtonText: "Yes, Delete it",
        
    }).then((result) => {
        if (result.value) {
            GALLERY_WALL_UPDATE({ name: item.name, url: item.name.replace(' ', '-'), profanity: item.url, status: item.status, autoUpdate: item.autoUpdate, id: item.id, deleted: 1 }).then((response) => {
                try {
                    const { responseData, responseCode, responseKey, upgradePopUp } = response.data;
                    if (responseCode == 200) {
                        UPDATE_EXTRA_DATA({ wallId: null }).then(res => {
                            this.props.getUserInfo()

                        }).catch(err => console.log(err))
                        // const remainData = allWalls.filter((x) => x.id !== item.id);
                        dispatch({type: DELETE_WALL, payload: id })
                        TOAST_SUCCESS('Gallery deleted successfully');
                        // this.setState({ allwalls: remainData, isopenUpgradPlan: upgradePopUp, id: 0, name: null, url: null, profanity: 0, submitErrorMessage: null, wallError: false, idEditGalleryRequest: false })
                        //this.props.getUserInfo()
                    }
                } catch (error) { }
            })
                .catch((error) => {
                    const { data } = error.response;
                    if (data.response_code == 404) { this.setState({ submitErrorMessage: data.message, apiRequest: false }) }
                    console.error(error);
                });
        }
        else if (result.dismiss === "cancel") { }
    });
  }

  const onChangeStatus = (item) => {
    const stat = statusWall == 1 ? 0 : 1
    setStatusWall(statusWall == 1 ? 0 : 1)
        GALLERY_WALL_UPDATE({ name: item.name, url: item.url, profanity: item.url, status: stat, autoUpdate: item.autoUpdate, id: item.id, deleted: 0 }).then((response) => {
            const { responseData, responseKey } = response.data;
            TOAST_SUCCESS(`Status ${responseData.status == 1 ? 'on' : 'off'} successfully.`);
            // this.setState({ statusWall: responseData.status })
            setStatusWall(responseData.status)
        })
            .catch((error) => {
                const { data } = error.response;
                if (data.response_code == 404) { this.setState({ statusWall: statusWall == 1 ? 0 : 1 }) }
                console.error(error);
            });
}


const onChangeAutoUpdate = (item) => { 
    const stat = autoUpdateWall == 1 ? 0 : 1
    setAutoUpdateWall(autoUpdateWall == 1 ? 0 : 1);
        GALLERY_WALL_UPDATE({ name: item.name, url: item.url, profanity: item.url, status: item.status, autoUpdate: stat, id: item.id, deleted: 0 }).then((response) => {
            const { responseData, responseKey } = response.data;
            TOAST_SUCCESS(`Auto update ${responseData.autoUpdate == 1 ? 'on' : 'off'} successfully.`);
            this.setState({ autoUpdateWall: responseData.autoUpdate })
            setAutoUpdateWall(responseData.status)

        })
            .catch((error) => {
                const { data } = error.response;
                console.error(error);
            });
     
}
    return   <li ref={outsideNewRef} >
                   <>
                   {/* <OverlayTrigger placement='right' overlay={<Tooltip>{item?.name}</Tooltip>}> */}
                    <Link className={`gallery_btn ${item.id == localStorage.getItem('wallId') ? "active" :""}`} to={`/content/moderation/${user.role == 'Collaborator' ? item.wallId : item.id}`} onClick={() => {selectOtherWidget(item); setEditPopover('')}}>
                        <span className='me-2'>
                            <svg xmlns="http://www.w3.org/2000/svg" width="19" height="17" viewBox="0 0 19 17" fill="none">
                                <path d="M6.34375 3.46875C5.39453 3.46875 4.65625 4.24219 4.65625 5.15625C4.65625 6.10547 5.39453 6.84375 6.34375 6.84375C7.25781 6.84375 8.03125 6.10547 8.03125 5.15625C8.03125 4.24219 7.25781 3.46875 6.34375 3.46875ZM16.7148 0.375H3.21484C1.98438 0.375 0.964844 1.39453 0.964844 2.625V13.875C0.964844 15.1406 1.98438 16.125 3.21484 16.125H16.7148C17.9453 16.125 18.9648 15.1406 18.9648 13.875V2.625C18.9648 1.39453 17.9805 0.375 16.7148 0.375ZM17.2773 13.6641L12.4609 7.125C12.3555 6.94922 12.1797 6.84375 11.9688 6.84375C11.7227 6.84375 11.5469 6.94922 11.4062 7.125L7.67969 12.1875L6.37891 10.5703C6.23828 10.4297 6.0625 10.3242 5.85156 10.3242C5.64062 10.3242 5.46484 10.4297 5.32422 10.5703L2.6875 13.875L2.65234 2.625C2.65234 2.34375 2.93359 2.0625 3.21484 2.0625H16.7148C17.0312 2.0625 17.2773 2.34375 17.2773 2.625V13.6641Z" fill="#6D7175"></path>
                            </svg>
                        </span>
                        {item?.name}
                    </Link> 
                    {/* </OverlayTrigger> */}
                    <span className='edit_popover' onClick={()=> setEditPopover(editPopover==item.id ? item.id: item.id)}>
                        <svg xmlns="http://www.w3.org/2000/svg" width="3" height="9" viewBox="0 0 3 9" fill="none">
                            <path d="M1.25 6.28125C1.83594 6.28125 2.34375 6.78906 2.34375 7.375C2.34375 7.98047 1.83594 8.46875 1.25 8.46875C0.644531 8.46875 0.15625 7.98047 0.15625 7.375C0.15625 6.78906 0.644531 6.28125 1.25 6.28125ZM1.25 3.15625C1.83594 3.15625 2.34375 3.66406 2.34375 4.25C2.34375 4.85547 1.83594 5.34375 1.25 5.34375C0.644531 5.34375 0.15625 4.85547 0.15625 4.25C0.15625 3.66406 0.644531 3.15625 1.25 3.15625ZM1.25 2.21875C0.644531 2.21875 0.15625 1.73047 0.15625 1.125C0.15625 0.539062 0.644531 0.03125 1.25 0.03125C1.83594 0.03125 2.34375 0.539062 2.34375 1.125C2.34375 1.73047 1.83594 2.21875 1.25 2.21875Z" fill="#4E69ED"/>
                        </svg>
                        {editPopover == item.id ? 
                            <div id="editPopover" className="custom_popover popover_editInner position-absolute top right">
                                <button type="button" onClick={()=>{ setEditPopover('') ; setEditGallery_Popover(item.id); onGalleryName(item.name)}}>
                                    <svg xmlns="http://www.w3.org/2000/svg" width="18" height="15" viewBox="0 0 18 15" fill="none">
                                        <path d="M6.09766 7.75C4.18359 7.75 2.625 6.19141 2.625 4.25C2.625 2.33594 4.18359 0.75 6.09766 0.75C8.01172 0.75 9.59766 2.33594 9.59766 4.25C9.59766 6.19141 8.01172 7.75 6.09766 7.75ZM7.49219 9.0625C8.77734 9.0625 9.92578 9.58203 10.7734 10.4023L9.51562 11.6328C9.32422 11.8242 9.21484 12.0703 9.16016 12.3438L8.75 14.2852C8.72266 14.4492 8.72266 14.6133 8.77734 14.75H0.929688C0.410156 14.75 0 14.3398 0 13.8203C0 11.1953 2.10547 9.0625 4.73047 9.0625H7.49219ZM10.1445 12.2617L13.5625 8.84375L15.5312 10.8125L12.1133 14.2305C12.0312 14.3125 11.9492 14.3398 11.8672 14.3672L9.89844 14.75C9.73438 14.8047 9.57031 14.6406 9.625 14.4766L10.0078 12.5078C10.0352 12.4258 10.0625 12.3438 10.1445 12.2617ZM17.1992 7.75C17.582 8.13281 17.582 8.76172 17.1992 9.14453L16.1602 10.1836L14.1914 8.21484L15.2305 7.17578C15.6133 6.79297 16.2422 6.79297 16.625 7.17578L17.1992 7.75Z" fill="#6D7175"/>
                                    </svg>
                                    <span>Edit</span>
                                </button> 
                                <button type="button" onClick={()=> deleteGallery(item.id)}>
                                    <svg xmlns="http://www.w3.org/2000/svg" width="13" height="15" viewBox="0 0 13 15" fill="none">
                                        <path d="M3.69141 1.24219C3.82812 0.941406 4.12891 0.75 4.45703 0.75H7.76562C8.09375 0.75 8.39453 0.941406 8.53125 1.24219L8.75 1.625H11.375C11.8398 1.625 12.25 2.03516 12.25 2.5C12.25 2.99219 11.8398 3.375 11.375 3.375H0.875C0.382812 3.375 0 2.99219 0 2.5C0 2.03516 0.382812 1.625 0.875 1.625H3.5L3.69141 1.24219ZM0.847656 4.25H11.375V13C11.375 13.9844 10.582 14.75 9.625 14.75H2.59766C1.64062 14.75 0.847656 13.9844 0.847656 13V4.25ZM3.03516 6.4375V12.5625C3.03516 12.8086 3.25391 13 3.47266 13C3.71875 13 3.91016 12.8086 3.91016 12.5625V6.4375C3.91016 6.21875 3.71875 6 3.47266 6C3.25391 6 3.03516 6.21875 3.03516 6.4375ZM5.66016 6.4375V12.5625C5.66016 12.8086 5.87891 13 6.09766 13C6.34375 13 6.5625 12.8086 6.5625 12.5625V6.4375C6.5625 6.21875 6.34375 6 6.09766 6C5.87891 6 5.66016 6.21875 5.66016 6.4375ZM8.3125 6.4375V12.5625C8.3125 12.8086 8.50391 13 8.75 13C8.96875 13 9.1875 12.8086 9.1875 12.5625V6.4375C9.1875 6.21875 8.96875 6 8.75 6C8.50391 6 8.3125 6.21875 8.3125 6.4375Z" fill="#6D7175"/>
                                    </svg>
                                    <span>Delete</span>
                                </button>
                                <div className="wall_toggle_action d-block pt-2 mt-2 border-top">
                                    <div className="d-flex align-items-center justify-content-between pr-1 py-1 px-2">
                                        <span>Auto Update</span>
                                        <span className="switch switch-xs">
                                            <label>
                                                <input type="checkbox" id="wallsautoUpdate" className="onoffswitch-checkbox updateStatus" defaultChecked={autoUpdateWall} onChange={()=> onChangeAutoUpdate(item)}  checked={autoUpdateWall} />
                                                <span></span>
                                            </label>
                                        </span>
                                    </div>
                                </div>
                                <div className="wall_status">
                                    <div className="d-flex align-items-center justify-content-between py-1 px-2">
                                        <span className="title_label">Status</span>
                                        <div className="text-right">
                                            <span className="switch switch-xs">
                                                <label>
                                                    <input className="form-check-input" type="checkbox" id="flexSwitchCheckDefault" onClick={() => onChangeStatus(item)} checked={statusWall} />
                                                    <span></span>
                                                </label>
                                            </span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        :''} 
                        {editGallery_Popover == item.id  ?
                            <div id="editGalleryPopover" className="custom_popover editGallery_Popover position-absolute top right">
                                <div className="content-popover">
                                    <p>Gallery Name</p>
                                    <input type="text" defaultValue={galleryName}  onChange={(e)=>onGalleryName(e.target.value)} />
                                    <span id="wallsnameerror" className="errorMessage required"></span>
                                    <div className="addGallerybtn text-end">
                                        <button type="button" className="btn btn_outline mr-4"  onClick={()=>{setEditPopover(''); setEditGallery_Popover('')}}>Close</button>
                                        <button type="button" className="btn btn_primary "  onClick={() =>{ onAddGalleryWall(item.id); setEditPopover(''); setEditGallery_Popover('')}} >Save</button>
                                    </div>
                                </div>
                            </div>
                        :''}

                    </span>
                    </> 

                </li> 
}

export default WallSwitcher